<script>
  import { scaleLinear } from "d3-scale";
  import { interpolateHcl } from "d3-interpolate";

  import Number from "./Number.svelte";
  import regionMeans from "./../data/means.json";
  import { map_legend_title, map_legend_subhead } from "./../content/data.json";

  // export let activeYearIndex = 0;

  const values = Object.values(regionMeans);

  const maxValue = Math.max(
    Math.abs(Math.min(...values)),
    Math.abs(Math.max(...values))
  );
  const roundedMaxValue = Math.round(maxValue / 100) * 100;

  $: colorScale = scaleLinear([
    "#269abe",
    "#1f8b8f",
    "#444",
    "#fa6138",
    "#ff0047"
  ])
    .domain([-maxValue, -maxValue / 2, 0, maxValue / 2, maxValue])
    .interpolate(interpolateHcl)
    .clamp(true);

  $: ticks = [-roundedMaxValue, ...colorScale.ticks(20), roundedMaxValue].map(
    d => [d, colorScale(d)]
  );
</script>

<div class="c">
  <div class="title">{map_legend_title}</div>
  <div class="subheading">{map_legend_subhead}</div>
  <div
    class="gradient"
    style="{`background: linear-gradient(to right, ${ticks
      .map(([v, color]) => color)
      .join(', ')})`}">
    <div class="dots">
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
    </div>
  </div>
  <div class="values">
    <div class="value">
      <Number number="{-roundedMaxValue}" format=",.0f" />
    </div>
    <div class="value">0</div>
    <div class="value">
      +
      <Number number="{roundedMaxValue}" format=",.0f" />
    </div>
  </div>
  <!-- {#each ticks as tick}{tick}{/each} -->
</div>

