<script>
  import { scaleLinear } from "d3-scale";
  import Number from "./Number.svelte";
  import data from "./../data/causes-of-death.json";
  import {
    bar_chart_heading,
    bar_chart_climate_change_label,
    bar_chart_footer
  } from "./../content/data.json";
  import warmingDeathratesRaw from "./../data/deathrates_fulladapt_rcp85_global_all_years_unstacked.csv";

  export let year = 2100;

  let warmingDeathrates = {};
  Object.keys(warmingDeathratesRaw[0])
    .filter(d => d)
    .forEach(year => {
      warmingDeathrates[year] = +warmingDeathratesRaw[0][year];
    });

  const climateChangeKey = "climate-change";
  const causes = [climateChangeKey, ...Object.keys(data)];

  const rowHeight = 18;

  $: xScale = scaleLinear()
    .domain([0, Math.max(...Object.values(data))])
    .range([0, 100]);

  let causeStats = {};
  const updateCauseStats = () => {
    causeStats = {};
    causes.forEach(cause => {
      const deaths =
        (cause == climateChangeKey ? warmingDeathrates[year] : data[cause]) ||
        0;
      causeStats[cause] = {
        width: xScale(deaths),
        deaths
      };
    });
    let sortedCauses = [...causes].sort(
      (a, b) => causeStats[b].deaths - causeStats[a].deaths
    );
    causes.forEach(cause => {
      const index = sortedCauses.indexOf(cause);
      causeStats[cause] = {
        ...causeStats[cause],
        index,
        y: index * rowHeight
      };
    });
  };

  $: year, xScale, updateCauseStats();
</script>

<div class="c">
  <h6>Causes of death</h6>
  <div class="subheading">per 100,000 people</div>
  <figure class="rows" style="{`height: ${causes.length * rowHeight}px`}">
    {#each causes as cause (cause)}
      <div
        class="row"
        class:active="{cause == climateChangeKey}"
        style="{`transform: translateY(${causeStats[cause].y}px)`}">
        <div
          class="label"
          style="{`transition-delay: ${causeStats[cause].index * 0.03}s`}">
          {cause == climateChangeKey ? bar_chart_climate_change_label : cause}
        </div>
        <div
          class="value"
          style="{`transition-delay: ${causeStats[cause].index * 0.03}s`}">
          {Math.round(causeStats[cause].deaths)}
        </div>
        <div class="bar" style="{`height: ${rowHeight - 3}px`}">
          <div
            class="bar-fill"
            style="{`width: ${causeStats[cause].width}%`}"></div>
        </div>
      </div>
    {/each}
  </figure>
  <!-- <div class="footer">{bar_chart_footer}</div> -->
</div>

