<script>
  import { onMount } from "svelte";
  import { debounce, scaleCanvas } from "./../utils";

  export let colors = [];

  let width = 1200;
  let height = 500;
  let canvasElement;

  const drawGradient = () => {
    if (!canvasElement) return;
    const ctx = canvasElement.getContext("2d");
    scaleCanvas(canvasElement, ctx, width, height);

    // some black and transparent noise
    const data = Uint32Array.from(
      { length: canvasElement.width * canvasElement.height },
      () => (Math.random() > 0.5 ? 0xff000000 : 0)
    );
    const img = new ImageData(
      new Uint8ClampedArray(data.buffer),
      canvasElement.width,
      canvasElement.height
    );

    ctx.putImageData(img, 0, 0);

    ctx.fillStyle = ctx.createLinearGradient(0, 0, 0, height);
    ctx.fillStyle.addColorStop(0.1, "black");
    ctx.fillStyle.addColorStop(0.9, "transparent");
    // apply transparency gradient on noise (darken top)
    ctx.globalCompositeOperation = "multiply";
    ctx.fillRect(0, 0, width, height);
    // apply black of the gradient on noise (dim bottom)
    ctx.globalCompositeOperation = "destination-in";
    ctx.fillRect(0, 0, width, height);

    // optionally change the color of the noise
    ctx.globalCompositeOperation = "source-atop";
    ctx.fillStyle = colors[0];
    ctx.fillRect(0, 0, width, height);
  };

  onMount(drawGradient);
  const debounceDrawGradient = debounce(drawGradient, 300);
  $: width, height, canvasElement, debounceDrawGradient();
</script>

<div class="c" bind:clientWidth="{width}" bind:clientHeight="{height}">
  <canvas
    bind:this="{canvasElement}"
    style="{`width: ${width}px; height: ${height}px`}"></canvas>
</div>

<style>
.c {
  width: 100%;
  height: 30em;
  background: white;
}</style>
