<script>
  import Scatter from "./Scatter.svelte";
  import Scroller from "./Scroller.svelte";
  import Text from "./Text.svelte";
  import * as copy from "./../content/data.json";

  let scrollIndex = 0;

  const groupOptions = copy.dots.map(d => ({
    text: d["text"],
    dims: d["dimensions"].split(",").map(d => d.trim()),
    xScaleLabel: d["x_scale_label"] || "",
    yScaleLabels: (d["y_scale_labels"] || "").split(",").map(d => d.trim())
  }));

  $: group = groupOptions[Math.min(scrollIndex, groupOptions.length - 1)] || [];
</script>

<div class="c">
  <Scroller bind:index="{scrollIndex}">
    <div slot="foreground">
      {#each groupOptions as { text }}
        <section>
          <div class="blurb blurb-bg">
            <Text {text} />
          </div>
          <div class="blurb">
            <Text {text} />
          </div>
        </section>
      {/each}
    </div>

    <div slot="background" class="background">
      <Scatter
        dims="{group.dims}"
        yScaleLabels="{group.yScaleLabels}"
        xScaleLabel="{group.xScaleLabel}" />
    </div>
  </Scroller>
</div>

