<script>
  export let options = [];
  export let value = null;
  export let onChange = () => {};

  const onChangeLocal = option => {
    onChange(option);
    value = option.id;
  };
</script>

<div class="c">
  {#each options as option}
    <button
      class="option"
      class:selected="{option.id == value}"
      on:click="{() => onChangeLocal(option)}">
      {option.label}
    </button>
  {/each}
</div>

<style>
.c {
  display: flex;
  align-items: center;
}
button.option {
  /* flex: 1; */
  padding: 0.8em 1em;
  line-height: 1.3em;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: white;
  font-weight: 600;
  border: 1px solid #f4f4f4;
  outline: none;
  cursor: pointer;
}
.option.selected {
  background: var(--c2);
  color: white;
}</style>
