<style>
@keyframes move {
  0% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 100%;
  }
}
.move {
  stroke-dasharray: 90 9;
  animation: move 26s ease-out;
  animation-iteration-count: infinite;
}

@keyframes popIn {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  80% {
    transform: scale(1.1);
    opacity: 0.3;
  }
  100% {
    transform: scale(1);
    opacity: 0.3;
  }
}
circle {
  transform: scale(0);
  animation: popIn 0.6s ease-out;
  animation-fill-mode: forwards;
}
circle:nth-of-type(2) {
  animation-delay: 0.4s;
}
circle:nth-of-type(3) {
  animation-delay: 0.8s;
}
@media (prefers-reduced-motion: reduce) {
  .move {
    animation: none;
  }
}</style>
