  color: #a1a1ac;
  font-weight: 100;
}
::placeholder {
  color: #a1a1ac;
  font-weight: 100;
}
.options {
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 30em;
  max-width: calc(100vw - 4em);
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
  box-shadow: var(--box-shadow);
  background: #eeeef3;
  padding: 0.6em;
  transform: translateY(103%);
  opacity: 0;
  pointer-events: none;
  transition: transform 0.3s ease-out, opacity 0.1s ease-out;
  z-index: 100;
}
.options.showing {
  opacity: 1;
  transform: translateY(100%);
  pointer-events: all;
}
.option {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  margin: 0.2em;
  padding: 0.5em 0.9em;
  background: white;
  font-size: 0.9em;
  line-height: 1.3;
  cursor: pointer;
  border-radius: 3px;
  border: 2px solid white;
  transition: all 0.2s ease-out;
}
.option.focused,
.option:focus,
.option:hover {
  color: var(--c2);
  border-color: var(--c2);
  outline: none;
}
.option.active {
  color: white;
  background: var(--c2);
  border-color: var(--c2);
}
